import React from 'react';
import {projects} from './Resources/data';
import './Stylesheet/app.css';

let index = 1 , path = "", explanation = [];

const display = (project) => { 
  switch(project.presentation){
    case "slideshow" : 
     explanation = require(`${path}/explanation.js`).text;
      return ( <div>
        <div id = "display" >  
          <div id = "slider">
            {[project.size,1,2].map((value, index)=>{ return (
              <img className = "images" src = {require(`${path}/Slides/${value}.png`)}/>
            )})}
          </div>
          <button className = "slideButtons" id = "right" 
            onClick = {() => {slide(project, "left")}}/> 
          <button className = "slideButtons" id = "left" 
            onClick = {() => {slide(project, "right")}}/>  
        </div>
      </div>);
    case "video" :
      path = require(`${path}/video.mkv`)
      return (<div id = "display">
        <video src={path} autoPlay loop muted />
      </div>);   
  }
}

const slide = (project, direction) => {
  const slideButtons = document.getElementsByClassName("slideButtons");
  const text = document.getElementById("explanation");
  for(let i = 0; i< 2; i++) slideButtons[i].disabled = true
  const cur = index;
  const left = (index === 1) ? project.size : index - 1;
  const right = (index === project.size) ? 1 : index + 1;
  const beforeLeft = (left === 1 ) ? project.size : left - 1;
  const afterRight = (right === project.size) ? 1 : right + 1;
  const [prev, next, replace] 
  = (direction === "left")? [left, right, afterRight] : [right, left, beforeLeft] ;
  const [prevpos, nextpos] = (direction === "left")? [0,2] : [2,0];
  const images = document.getElementsByClassName("images");
  const slider = document.getElementById("slider");
  slider.style.transitionDuration = "2s";
  slider.style.marginLeft = (direction === "left") ? "-50%" : "150%";
  text.style.opacity = 0;
  setTimeout(() => {
    text.innerHTML = `${next}. ${explanation[next - 1]}`;
    text.style.opacity = 1;}, 1000);
  setTimeout(() =>{
    images[prevpos].src = require(`${path}/Slides/${cur}.png`)
    images[1].src = require(`${path}/Slides/${next}.png`)
    slider.style.transitionDuration = "0s";
    slider.style.marginLeft = "50%";
    images[nextpos].src = require(`${path}/Slides/${replace}.png`)
    index = next;
    for(let i = 0; i< 2; i++) slideButtons[i].disabled = false
  }, 2000);    
}

const displayLinks = (links) => {
  if(links === null) return
  return(<div id = "links">
  {links.map((value, index)=>{return (
    <span>click <a href = {value.address}>here</a> to {value.action}</span>
  )})}
  </div>)
}

const explained = (project) => {
  if(project.presentation === "video")return;
  return <span id = "explanation">{`1. ${explanation[0]}`}</span>
}

const tools = (tools) => {
  if(tools === null) return
  return <div id = "tools"><span  >Tools used in this project are {tools}</span></div>  
}

export const App = () => {
  const id = window.location.href.split("?")[1]
  const project = projects[id]; 
  if(!project) return <img id = "errmsg" src = {require("./Resources/Images/errmsg.png")}/>
  path = `./Resources/ProjectAssets/${project.name}`;
  const gotoLink = "https://raghu412.github.io/resume/";
  const status = (project.status === "ongoing")?"#00AE86":"#465B69";
  let goto = [
    {"title" : "view all projects","link":`${gotoLink}?projects`},
    {"title" : "view resume","link":gotoLink},
    {"title" : "contact me","link":`${gotoLink}?contact`}
  ]
  
  return ( <div id = "app"> 
    <div id = "title" style = {{background : project.accent}}><h1>{project.name}</h1></div>
    <div id = "container">
    <div className = "border" />
      {display(project)}
    <div className = "border" >{explained(project)}</div>
    <div id = "status"> <span style = {{color:status}}><b>{project.status}</b></span>
    </div>
    <div id = "short"><p>{project.short}</p></div>
    
    <div id = "detailed"><p><pre>{project.detailed}</pre></p></div>
    {tools(project.tools)}
    {displayLinks(project.links)}
    <div id = "stamp" >
      <img src = {require("./Resources/Images/stamp.png")} width = "300px"/>
    </div>
    </div>
    <div id = "goto" style = {{background : project.accent}}> <div>
      {goto.map((value, index)=>{return (
        <button onClick = { () => 
          {window.location.href=value.link}}>{value.title}</button>)})}
  </div> </div>);  
  </div>);
}